import common from '../../common/common.js'

export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			info:{
				icon_color:'white',
				delta:1,
			},
			scrollTop:0,		//滚动高度
			header_bg_alpha:0,	//头部背景色透明度
			currentPagesLen:1,	//页面数量，看是否返回
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,
			
			fid:0,					//大类ID
			cid:0,					//小类ID
			tempCurrentCatList:[],	//切换类别时的临时数组
			currentCatList:[],		//当前滚动的类别列表
			bigCatList:[],			//大类别列表
			subCatList:[],			//小类列表
			cindex:-1,				//当前选择中类别
			findex:0,				//弹出层切换类别参数
			scrollLeft:0,			//子栏目滚动距离
			
			shopList:[],			//店铺列表
			isall:0,				//是否显示完
			page:0,					//当前页码
			pop_category:false,
			
			canbuy:1,
		};
	},
	onLoad(e) {
		var that = this
		var fid = e.fid			
		if(fid > 0){
			that.fid = fid
		}
		var cid = e.cid
		console.log("cid",e)
		if(cid > 0){
			that.cid = cid
		}
		
		
		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;
		
		//获取类别列表
		that.getCatList();
		//获取店铺列表
		that.getCatShopList();
		
		// var currentPages = getCurrentPages()
		// var currentPagesLen = currentPages.length
		// that.currentPagesLen = currentPagesLen;
		
		
	},
	// onPageScroll(e) {
	// 	let that = this
	// 	that.scrollTop = e.scrollTop
	// 	if(e.scrollTop < 100){
	// 		that.header_bg_alpha = e.scrollTop/100
	// 	}else{
	// 		that.header_bg_alpha = 1
	// 	}
	// 	console.log(that.header_bg_alpha)
	// },
	
	onPullDownRefresh:function(){
	  var that = this
	  uni.showNavigationBarLoading();
	  
	  that.resetAttr();
	  //获取类别列表
	  that.getCatList();
	  //获取店铺列表
	  that.getCatShopList();
	  
	  console.log('下拉刷新中...');		  
	  uni.hideNavigationBarLoading();
	  uni.stopPullDownRefresh();
	},
	onShareAppMessage(res) {
		var that = this
		if (res.from === 'button') {// 来自页面内转发按钮
		  console.log(res.target)
		}
		var userInfo = uni.getStorageSync('userInfo');
		if(userInfo.users_id > 0){
			var shareUrl = '/pages/merchant/merchant?tjuid='+userInfo.users_id
		}else{
			var shareUrl = '/pages/merchant/merchant'
		}
		return {
		  title: '商家活动广告平台——众晨星',
		  path: shareUrl,
		}
	},
	onReachBottom(){
		var that = this
		that.loadmore();
	},
	methods: {
		//切换弹出层小类
		changePopCat(e){
			var that = this
			var cindex = e.currentTarget.dataset.cindex
			var fid = e.currentTarget.dataset.fid
			var cid = e.currentTarget.dataset.cid
			if(cid != that.cid){
				
				if(fid != that.fid){
					//切换大类及小类
					that.fid = fid
					that.currentCatList = that.subCatList[that.findex]
					
				}
				//只切换小类
				that.cid = cid
				that.cindex = cindex
				that.scrollLeft = cindex*75;
				that.resetAttr();
				//重新获取数据
				that.getCatShopList();
				that.togglePopCategory();
			}
		},
		//
		togglePopCategory(){
			var that = this
			that.pop_category = !that.pop_category
		},
		//切换小类
		changePopBigCat(e){
			var that = this
			var index = e.currentTarget.dataset.index
			var subCatList = that.subCatList
			that.tempCurrentCatList = subCatList[index]
			that.findex = index
		},
		//切换小类
		changeSubCat(e){
			var that = this
			var cid = e.currentTarget.dataset.cid
			var cindex = e.currentTarget.dataset.cindex
			that.cid = cid
			that.cindex = cindex
			that.scrollLeft = cindex*75;
			that.resetAttr();
			that.getCatShopList();
		},
		//重置参数
		resetAttr(){
			var that = this
			that.page = 0;
			that.isall = 0;
			that.shopList = []
		},
		//获取类别列表
		getCatList(){
			var that = this
			uni.request({
				url: common.apiUrl + 'shop/getCatList',
				method: 'GET',
				data: {
					fid:that.fid
				},
				success: res => {
					var data = res.data
					console.log(data)
					that.bigCatList = data.big_cat_list
					that.subCatList = data.sub_cat_list
					that.currentCatList = data.current_cat_list
					that.tempCurrentCatList = data.current_cat_list
					that.fid = data.big_cat_list != '' ? data.big_cat_list[0]['cat_id'] : 0;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//加载更多
		loadmore(){
			var that = this
			that.page = that.page+1;
			that.getCatShopList();
		},
		//获取店铺列表
		getCatShopList(){
			var that = this
			var lng = uni.getStorageSync('lng');
			var lat = uni.getStorageSync('lat');
			uni.request({
				url: common.apiUrl + 'shop/getCatShopList',
				method: 'GET',
				data: {
					fid:that.fid,
					cid:that.cid,
					lng:lng,
					lat:lat,
					page:that.page+1
				},
				success: res => {
					var data = res.data
					console.log(data)
					var shopList = data.shop_list
					for(var i=0;i<shopList.length;i++){
						that.shopList.push(shopList[i])
					}						
					that.isall = data.isall
					that.canbuy = data.canbuy
					console.log("shopList",that.shopList)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//电话联系
		phoneContact(e){
			var that = this
			var index = e.currentTarget.dataset.index
			var shopList = that.commendShopList
			var mobile = e.currentTarget.dataset.mobile
			uni.makePhoneCall({
			    phoneNumber: mobile 
			});
		},
		//一键导航
		goaddr(e){
			var that = this
			var index = e.currentTarget.dataset.index
			var shopList = that.shopList
			var lat = shopList[index].shop_lat
			var lng = shopList[index].shop_lng
			
			console.log(parseFloat(lat));
			console.log(parseFloat(lng));
			uni.openLocation({
			    latitude: parseFloat(lat),
			    longitude: parseFloat(lng),
			    success: function () {
			        console.log('success');
			    }
			});
		},
		navBack(){
			uni.navigateBack({
				delta:1
			})
		},
		navHome(){
			uni.switchTab({
				url:'/pages/index/index'
			})
		},
		//跳转
		jumpUrl(e){
			var that = this
			if(that.canbuy == 1){
				var url = e.currentTarget.dataset.url
				uni.navigateTo({
					url:url,
				});
			}
			
		}
	}
}
